<template>
  <div class="reset-psw-view view-background">
    <header-view :fixed="fixed" :title="title" :routerName="routerName"></header-view>
    <tab-view :action="tabAction" @changeTab="changeTab" :activeId="tabId"></tab-view>
    <div id="mychart" class="chart"></div>
    <ul class="details-list">
      <li><span class="details-type"></span><span></span><span></span></li>
      <li v-for="item in detailsList">
        <div class="type-date"><span class="item-type">{{item.profitType}}</span><span>{{item.takeTime}}</span></div><span>{{item.joyScore}}</span><span class="item-balance">{{item.totalJoyScore}}</span></li>
    </ul>
  </div>
</template>

<script>
  import header from '@/components/header/header'
  import tab from '@/components/tab/tab'
  import {getProfitChart,getProfitList} from '../../service/getData'
  
  
  let echarts = require('echarts/lib/echarts')
  // 
  require('echarts/lib/chart/line')
  // title
  require('echarts/lib/component/tooltip')
  require('echarts/lib/component/title')
  
  
  export default {
    components: {
      'tab-view': tab,
      'header-view': header
    },
    data () {
      return {
        title: '',
        fixed: false,
        routerName: 'custom',
        chartsList: [],
        detailsList: [],
        chartXList: [],
        chartYList: [],
        echartOption: [],
        myChart:null,
        tabId: '7',
        days: '7',
        tabAction: [
          {
            id: '7',
            categoryName: '7'
          },
          {
            id: '15',
            categoryName: '15'
          },
          {
            id: '30',
            categoryName: '30'
          }
        ]
      }
    },
    created: function () {
      var _this = this
      if (this.$route.params.name) {
        this.routerName = this.$route.params.name // 
        localStorage.prev = this.$route.params.name
      } else {
        this.routerName = localStorage.prev
      }
      /*chartXList = ['08-18', '08-20', '08-21', '08-22', '08-23', '08-24', '08-25']
      chartYList = ['500', '200', '300', '-44', '-330', '86', '50']*/
      //console.log(chartXList)
      this.getProfitListData(this.days);
    },
    mounted: function () {
      this.getChartData(this.days);
    },
    methods: {
      setting: function (val, param) {
        this.$router.push({
          name: val,
          params: {
            setparams: param,
            prev: this.$route.name
          }
        })
      },
      getChartData : function(days) {
        var _this = this
        getProfitChart(days)
        .then(function (rep) {
          _this.chartsList = rep.data
          _this.chartXList = []
          _this.chartYList = []
          for(var i=0;i<_this.chartsList.length;i++){
             _this.chartXList.push(_this.chartsList[i].takeTime)
             _this.chartYList.push(_this.chartsList[i].joyScore)
          }
          _this.drawLine()
          _this.myChart.setOption({
              xAxis: {
                  data: _this.chartXList
              },
              series: [{
                  data: _this.chartYList
              }]
          })
          _this.myChart.hideLoading();
        })
        .catch(function () {
          _this.$toast('')
        })
      },
      getProfitListData: function(days){
        var _this = this
        getProfitList(days)
        .then(function (rep) {
          _this.detailsList = rep.data
        })
        .catch(function () {
          _this.$toast('')
        })
      },
      changeTab: function (val) { // tab
        this.tabId = val
        this.days = val
        this.getChartData(this.days)
        this.getProfitListData(this.days)
      },
      drawLine: function () {
        // domecharts
        if(this.myChart){
          this.myChart = null
          this.echartOption = []
        }
        this.myChart = echarts.init(document.getElementById('mychart'))
        // 
        this.echartOption = {
          backgroundColor: '#fff',
          tooltip: {
            trigger: 'axis',
            backgroundColor: '#FF8B20',
            formatter: function (params) {
              return params[0].value
            }
          },
          title: {
            left: 'center',
            text: ''
          },
          grid: { // 
            left: '11%',
            right: '5%',
            top: '5%',
            bottom: '15%'
          },
          xAxis: {
            type: 'category', // 
            boundaryGap: false,
            axisLine: {
              show: false,
              onZero: false
            },
            axisLabel: {
              textStyle: {
                color: '#A5A5A5'
              }
            },
            axisTick: {
              show: false // 
            },
            data: []
          },
          yAxis: {
            type: 'value',
            boundaryGap: false,
            axisLine: {
              show: false
            },
            splitLine: {
              show: true, // y
              lineStyle: {
                color: '#FFE9D4',
                type: 'dashed'
              }
            },
            axisLabel: {
              textStyle: {
                color: '#A5A5A5'
              }
            },
            axisTick: {
              show: false // 
            }
          },
          calculable: true,
          series: [
            {
              name: '',
              type: 'line',
              smooth: false, // 
              sampling: 'average',
              itemStyle: {
                normal: {
                  color: 'rgb(255, 143, 40)'
                }
              },
              areaStyle: {
                normal: {
                  color: 'rgb(255, 244, 233)'
                }
              },
              data: []
            }
          ]
        }
        this.myChart.showLoading();
        this.myChart.setOption(this.echartOption,true)
      }
    }
  }
</script>

<style>
  .chart {
    width: 100%;
    height: 32.68%;
    background-color: white;
  }
  
  .details-list {
    background-color: white;
    margin-top: .05rem;
    height: 53%;
    overflow: auto;
  }
  
  .details-list:first-child {
    display: flex;
    border-bottom: .01rem solid rgba(0, 0, 0, .1);
  }
  
  .details-list li {
    display: flex;
    border-bottom: .01rem solid rgba(0, 0, 0, .1);
  }
  
  .details-list li>span {
    line-height: .4rem;
    text-align: center;
    display: inline-block;
    flex: 1;
  }
  
  .details-type {
    text-align: left!important;
    flex: 2!important;
    padding-left: .15rem;
  }
  
  .item-type {
    color: #333;
    display: block!important;
    text-align: left!important;
    line-height: .15rem!important;
  }
  
  .item-balance {
    color: #ff8b20;
  }
  
  .type-date {
    flex: 2;
    height: .54rem;
    padding: .12rem 0 .0rem 0.15rem;
  }
  
  .type-date>span {
    font-size: .12rm;
  }
</style>


